const TextToSpeech = (props) => {
  const speechSynthesis = window.speechSynthesis;
  const [selectedOption, setSelectedOption] = useState(null);
  const [text, setText] = useState('');
  const [screenWakeLock, setScreenWakeLock] = useState(null);
  const [textInput, setTextInput] = useState('');
  const [fileInput, setFileInput] = useState(null);
  const [url, setUrl] = useState('');
  const [voices, setVoices] = useState([]);
  const [selectedVoice, setSelectedVoice] = useState(null);
  const [languageOptions, setLanguageOptions] = useState([]);
  const [voiceOptions, setVoiceOptions] = useState([]);
  const [selectedLanguage, setSelectedLanguage] = useState(null);
  const [rate, setRate] = useState(1);
  const [pitch, setPitch] = useState(1);
  const [showTab, setShowTab] = useState(1);
  const [update, setUpdate] = useState(1);
  const [paused, setPaused] = useState(false);
  const [corsProxy, setCorsProxy] = useState("https://corsproxy.io/?");

  const handleTextChange = (event) => {
    setTextInput(event.target.value);
    setText(event.target.value);
  };

  const handleSpeakClick = () => {
    if (cannotSpeak()) {
      return;
    }
    speechSynthesis.cancel();
    speakInChunk(text);
  };

  const speakInChunk = (originalText) => {
    const limit = 1200;
    var sentences = originalText.split('.');
    var currentChunk = '';
    for (let i = 0; i < sentences.length; i++) {
      currentChunk = currentChunk + sentences[i];
      if (currentChunk.length > limit || (i + 1 == sentences.length)) {
        speechSynthesis.speak(makeUtterance(currentChunk));
        currentChunk = '';
      }
    }
    startResuming();
  }

  const makeUtterance = (text) => {
    const utterance = new SpeechSynthesisUtterance(text);
    utterance.onstart = function (event) {
      setPaused(false);
      requestWakeLock();
      setPaused(false);
    };
    utterance.onend = function (event) {
      releaseWakeLock();
      setUpdate(update + 1);
      setPaused(false);
    };
    utterance.voice = selectedVoice;
    utterance.pitch = pitch;
    utterance.rate = rate;
    return utterance;
  }

  const startResuming = () => {
    const r = setInterval(() => {
      if (!speechSynthesis.speaking) {
        clearInterval(r);
      } else {
        //speechSynthesis.pause();
        speechSynthesis.resume();
        setPaused(false);
      }
    }, 10000);
  }

  const stop = () => {
    if (cannotStop()) {
      setUpdate(update + 1);
      return;
    }
    speechSynthesis.cancel();
    releaseWakeLock();
    setPaused(false);
  };

  const pause = () => {
    if (cannotPause()) {
      setUpdate(update + 1);
      return;
    }
    speechSynthesis.pause();
    releaseWakeLock();
    setPaused(true);
  };

  const resume = () => {
    if (cannotResume()) {
      setUpdate(update + 1);
      return;
    }
    if (paused) {
      speechSynthesis.resume();
      requestWakeLock();
      startResuming();
      setPaused(false);
    }
  };

  const handleVoiceChange = (option) => {
    setSelectedVoice(voices.find(voice => voice.name === option.value));
    setSelectedOption(option);
  };

  const handleLangChange = (option) => {
    const lang = option.value;
    setSelectedVoice(null);
    setSelectedOption(null);
    setSelectedLanguage(null);
    const voiceOptions = voices.filter(voice => voice.lang === lang).map(voice => (
      {value: voice.name, label:voice.name}
    ))
    setVoiceOptions(voiceOptions)
    speechSynthesis.getVoices().forEach(voice => {
      if (voice.lang === lang) {
        setSelectedLanguage(voice);
      }
    });
  };

  const handleProxyChange = (option) =>{
    var selectedProxy = option.value;
    while (!selectedProxy) {
      selectedProxy = proxyUrl[Math.floor(Math.random() * proxyUrl.length)];
    }
    setCorsProxy(selectedProxy);
  }


  const handleFileSelect = (event) => {
    const file = event.target.files[0];
    if (file != null) {
      setFileInput(file);
      console.log(file)
      const reader = new FileReader();
      if (file.name.endsWith('.txt')) {
        reader.onload = (event) => {
          const result = event.target.result;
          //pdfToText(result);
        };
        reader.readAsText(file);
      } else if (file.name.endsWith('.pdf')) {
        reader.onload = (event) => {
          const result = event.target.result;
          const fileContent = typeof result === "string" ? result : new TextDecoder("utf-8").decode(result);
          setText(fileContent);
        };
        reader.readAsArrayBuffer(file);
      }
      event.target.value = null;
    }
  };

  const requestWakeLock = async () => {
    try {
      setScreenWakeLock(await navigator.wakeLock.request("screen"));
    } catch (err) {

    }
  }

  const releaseWakeLock = () => {
    if (screenWakeLock !== null) {
      screenWakeLock.release()
        .then(() => {
          setScreenWakeLock(null);
        })
    }
  }

  const handleFileDownload = async () => {
    if (cannotDownload) {
      return;
    }
    const blob = await new Promise(async resolve => {
      console.log("picking system audio");
      // @ts-ignore
      const stream = await navigator.mediaDevices.getDisplayMedia({video:true, audio:true});
      const track = stream.getAudioTracks()[0];
      if(!track)
          throw "System audio not available";
      
      stream.getVideoTracks().forEach(track => track.stop());
      
      const mediaStream = new MediaStream();
      mediaStream.addTrack(track);
      
      const chunks = [];
      const mediaRecorder = new MediaRecorder(mediaStream, {bitsPerSecond:128000});
      mediaRecorder.ondataavailable = event => {
          if (event.data.size > 0)
              chunks.push(event.data);
      }
      mediaRecorder.onstop = () => {
          stream.getTracks().forEach(track => track.stop());
          mediaStream.removeTrack(track);
          resolve(new Blob(chunks));
      }
      mediaRecorder.start();
      
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.onend = () => mediaRecorder.stop();
      window.speechSynthesis.speak(utterance);
      console.log("speaking...");
    });

    const url = URL.createObjectURL(blob)
    const a = document.createElement('a');
    a.style.display = 'none';
    a.href = url;
    a.download = 'speech.webm';
    document.body.appendChild(a);
    a.click();
  }

  const onVoicesChanged = () => {
    const availableVoices = speechSynthesis.getVoices();
    setVoices(availableVoices);
    const availableLanguages = new Set<string>(availableVoices.map(voice => voice.lang));
    const sortedLanguages = new Set<string>(Array.from(availableLanguages).sort())
    const langOptions = [...sortedLanguages].map(lang => (
      {value:lang ,label:langCodeToEnglish(lang)}
    ))
    setLanguageOptions(langOptions);
  };

  // Load available voices and languages on component mount
  React.useEffect(() => {
    const sUserAgent = navigator.userAgent;
    const isChrome = sUserAgent.indexOf("Chrome") > -1
    onVoicesChanged();
    fetch("https://type.fit/api/quotes")
    .then(function(response) {
      return response.json();
    })
    .then(function(data) {
      var quoteOfTheDay = [
        'Tomorrow will be a great day!',
        'Youre braver than you believe, and stronger than you seem, and smarter than you think.',
        'Keep your face to the sunshine and you cannot see a shadow.',
        'The only time you fail is when you fall down and stay down.',
        'Positive anything is better than negative nothing.',
        'Happiness is an attitude. We either make ourselves miserable, or happy and strong. The amount of work is the same.',
        'Its not whether you get knocked down, its whether you get up.',
        'The happiness of your life depends upon the quality of your thoughts.',
        'You are never too old to set another goal or dream a new dream.'
      ]
      const quotes = data.map(quote => (quote.text));
      var randomQuote = quotes[Math.floor(Math.random() * quotes.length)];
      setTextInput(randomQuote);
      setText(randomQuote);
    });
    if (!isChrome) {
      return onVoicesChanged;
    }
    if (speechSynthesis.onvoiceschanged !== undefined) {
      speechSynthesis.onvoiceschanged = onVoicesChanged;
    }
    return () => {
      speechSynthesis.onvoiceschanged = null;
    };
  }, []);

  const handleChangeTab = (tabIndex) => {
    setShowTab(tabIndex);
    if (tabIndex === 1) {
      setText(textInput);
    } else if (tabIndex === 2) {
      setFileInput(null);
      setText('');
    } else if (tabIndex === 3) {
      setUrl('');
      setText('');
    }
  }

  const selectStyle = {
    indicatorsContainer: (baseStyles, state) => ({
      display: 'none',
    }),
    control: (baseStyles, state) => ({
      ...baseStyles,
      borderRadius: '1rem',
      borderColor: '#ffffff !important',
      boxShadow: "rgb(0 0 0 / 15%) 0px 0.3rem 0.3rem",
      zIndex: 1,
      backgroundColor: 'white',
      opacity: state.isDisabled ? '.4' : '1',
    }),
    menuList: (baseStyles, state) => ({
      ...baseStyles,
      borderRadius: '1rem',
      paddingTop: '0',
      paddingBottom: '0',
      zIndex: 2,
    }),
    menu: (baseStyles, state) => ({
      ...baseStyles,
      borderRadius: '1rem',
      marginTop: '0',
      zIndex: 2,
    }),
    option: (baseStyles, state) => ({
      ...baseStyles,
      textOverflow: 'ellipsis',
      overflow: 'hidden', 
      fontSize: '1.1rem',
      whiteSpace: 'nowrap',
      color: state.isFocused ? 'white' : 'black',
      backgroundColor : state.isFocused ? '#' + props.theme + 'cc': 'white',
      zIndex: 2,
    })
  };

  const sliderContainerStyle = {
    backgroundColor: 'white',
    boxShadow: "rgb(0 0 0 / 15%) 0px 0.3rem 0.3rem",
    zIndex: 1,
    borderRadius: "1rem",
    color: '#' + props.theme + 'ff',
    fontWeight: 'bold',
  };

  const sliderStyle = {
    color: '#' + props.theme + 'ff',
  }

  const cannotSpeak = () => {
    return text === "" || !selectedVoice || speechSynthesis.speaking;
  }

  const cannotPause = () => {
    return text === "" || !selectedVoice || !speechSynthesis.speaking || paused;
  }

  const cannotResume = () => {
    return text === "" || !selectedVoice || !speechSynthesis.speaking || !paused;
  }

  const cannotStop = () => {
    return text === "" || !selectedVoice || !speechSynthesis.speaking;
  }

  const cannotDownload = () => {
    return true;
  }

  const handleRateChange = (event: Event, newValue: number | number[]) => {
    setRate(newValue as number);
  };

  const handlePitchChange = (event: Event, newValue: number | number[]) => {
    setPitch(newValue as number);
  };

  const handleUrlChange = (event) => {
    const url = event.target.value;
    setUrl(url);
    
  }

  const cleanHtmlData = (htmlData) => {
    let body = new DOMParser().parseFromString(htmlData, 'text/html').getElementsByTagName("body")[0];
    removeNode(body.getElementsByTagName("button"));
    removeNode(body.getElementsByTagName("script"));
    removeNode(body.getElementsByTagName("noscript"));
    removeNode(body.getElementsByTagName("a"));
    removeNode(body.getElementsByTagName("link"));
    removeNode(body.getElementsByTagName("select"));
    removeNode(body.getElementsByTagName("li"));
    removeNode(body.getElementsByTagName("ul"));
    removeNode(body.getElementsByTagName("input"));
    removeNode(body.getElementsByTagName("ins"));
    removeNode(body.getElementsByTagName("label"));
    return body.textContent.replace(/\s+/g,' ').trim() || "";
  }

  const removeNode = (nodes) => {
    for (var J=nodes.length-1;  J >= 0;  J--) { //-- Kill the last, first, to avoid orphan problems.
      var node = nodes[J];
      if (node)
      {
        node.parentNode.removeChild(node);
      }
    }
  }

  const handleUrlFetch = () => {
    try {
      fetch(corsProxy + url)
      .then(function(response) {
        return response.text();
      }).then(function(data) {
        const sanitizedData = cleanHtmlData(data);
        setText(sanitizedData);
      })
    } catch (error) {
      setText('Error! Please check the url or try another CORS proxy!')
    }
  }

  return (
    <div className="tts-container">
      <p className="disclaimer-container">
        ** Supported languages depends on your system and browser</p>
      <div className="select-input">
        <Select className="lang-select" isSearchable={false} onChange={handleLangChange} 
          options={languageOptions} placeholder="Select Language" styles={selectStyle}/>
        <Select className="voice-select" name="voice-selected" isSearchable={false} 
          onChange={handleVoiceChange} options={voiceOptions} isDisabled={selectedLanguage == null} 
          placeholder="Select Voice" value={selectedOption} styles={selectStyle}/>
      </div>
      <div className="tab-selection paddingTop">
        <button className="tab-button" style={{borderTopLeftRadius: '1rem', backgroundColor: showTab === 1 ? 'white' : '#0000001a', color: showTab === 1 ? '#' + props.theme + 'ff' : 'white'}} 
          onClick={() => handleChangeTab(1)}>TEXT</button>
        <button className="tab-button" style={{backgroundColor: showTab === 2 ? 'white' : '#0000001a', color: showTab === 2 ? '#' + props.theme + 'ff' : 'white' }} 
          onClick={() => handleChangeTab(2)}>FILE</button>
        <button className="tab-button" style={{borderTopRightRadius: '1rem', backgroundColor: showTab === 3 ? 'white' : '#0000001a', color: showTab === 3 ? '#' + props.theme + 'ff' : 'white'}} 
          onClick={() => handleChangeTab(3)}>URL</button>
      </div>
      <div className="input-container" style={{display: showTab === 1 ? "block" : "none"}}>
        <textarea value={textInput} onChange={handleTextChange}
          style={{resize: "none", borderRadius: "1rem", padding: "0 1.5rem", border: 'none'}}/>
      </div>
      <div className="input-container" style={{display: showTab === 2 ? "block" : "none"}}>
        <div className="external-loader-container">
          <label htmlFor="file-upload" className="external-loader">
            <input type="file" id="file-upload" onChange={handleFileSelect} accept=".txt" hidden/>
            <div className="file-select-display-container">
                <img alt="" className="file-upload-icon" src="img/upload-icon.png" style={{color: '#' + props.theme + 'ff'}}/>
            </div>
            <p className="selected-file-name">{fileInput != null ? fileInput.name : 'No file selected'}</p>
          </label>
          <p className="disclaimer-container">
            ** Only .txt file is supported <br/>
            ** Your file and content will NOT be transmitted or stored</p>
        </div>
      </div>
      <div className="input-container" style={{display: showTab === 3 ? "block" : "none"}}>
        <div className="external-loader-container">
          <div className="url-loader">
            <div className="url-input-container">
              <div className="url-label" style={{color: '#' + props.theme + 'ff'}}>CORS Proxy</div>
              <Select className="url-input" isSearchable={false} onChange={handleProxyChange} 
                options={corsProxies} placeholder="Select Proxy" styles={selectStyle}/>
            </div>
            <div className="url-input-container">
              <div className="url-label" style={{color: '#' + props.theme + 'ff'}}>Page URL</div>
              <input className="url-input url-text-input" type="text" id="url" onChange={handleUrlChange} placeholder="https://the-real-han.github.io/"/>
            </div>
            <button onClick={handleUrlFetch} className="url-button" style={{backgroundColor: '#' + props.theme + 'ff'}}>Process URL</button>
            <div className="preview-text">{text ? text.substring(0, 100) : 'No preview avaiable'}</div>
          </div>
          <p className="disclaimer-container">
            ** Certain texts may or may not be included when processing<br/>
            ** Your URL and content will NOT be transmitted or stored</p>
        </div>
      </div>
      <div className="option-container">
      <Box sx={sliderContainerStyle} className="slider-container">
        <Stack spacing={2} direction="row" alignItems="center">
          <label htmlFor="rate">Rate</label>
          <Slider sx={sliderStyle} aria-label="rate" value={rate} onChange={handleRateChange} min={.5} max={2} step={.1} valueLabelDisplay="auto"/>
        </Stack>
        </Box>
        <Box sx={sliderContainerStyle} className="slider-container">
        <Stack spacing={2} direction="row" alignItems="center">
          <label htmlFor="rate">Pitch</label>
          <Slider sx={sliderStyle} aria-label="pitch" value={pitch} onChange={handlePitchChange} min={0} max={2} step={.1} valueLabelDisplay="auto"/>
        </Stack>
        </Box>
      </div>
      <div className="paddingTop control-container">
        <img alt="speak" className={cannotSpeak() ? "control-icon img-disabled" : "control-icon"}  src="img/play.png" onClick={handleSpeakClick}/>
        <img alt="pause" className={cannotPause() ? "control-icon img-disabled" : "control-icon"}  src="img/pause.png" onClick={pause}/>
        <img alt="resume" className={cannotResume() ? "control-icon img-disabled" : "control-icon"}  src="img/resume.png" onClick={resume}/>
        <img alt="stop" className={cannotStop() ? "control-icon img-disabled" : "control-icon"}  src="img/stop.png" onClick={stop}/>
        <img alt="download" className={cannotDownload() ? "control-icon img-disabled" : "control-icon"}  src="img/download.png" onClick={handleFileDownload}/>
      </div>
    </div>
  );
};

export default TextToSpeech;
