import './App.css';
import TextToSpeech from './component/TextToSpeech.tsx';
import React, {useState} from 'react';
import SocialNetWorkSharing from './component/SocialNetworkSharing'

function App() {

  const THEMES = {
    "theme-green" : "43e629",
    "theme-blue" : "1ecbe1",
    "theme-orange" : "fbb408",
    "theme-dark-blue" : "07498b",
    "theme-dark-green" : "1d5818",
    "theme-dark-red" : "6d0000",
  }

  const [theme, setTheme] = useState(Math.floor(Math.random() * Object.keys(THEMES).length))
  function nextTheme() {
    setTheme((parseInt(theme) + 1) % Object.keys(THEMES).length)
  }

  return (
    <div className={"main " + Object.keys(THEMES)[parseInt(theme)]}>
      <div className="home">
        <div className="main-content">
            <div className="title-block">
              <img alt="" className="speechup-logo" src="img/speechup-logo.png" onClick={() => nextTheme()}/>
              <img alt="" className="speechup-description" src="img/description.png"/>
            </div>
            <TextToSpeech theme={THEMES[Object.keys(THEMES)[parseInt(theme)]]}/>
          </div>
          <SocialNetWorkSharing />
      </div>
    </div>
  );
}

export default App;
